<template>
  <div class="layout">
    <Layout>
      <Sider ref="side1" hide-trigger collapsible :collapsed-width="78" v-model="isCollapsed"
      :style = "{position: 'fixed', height: '100vh', left: 0, overflow: 'auto'}">
        <app-menu :isCollapse="isCollapsed"></app-menu>
      </Sider>
      <Layout :style="[layoutStyle]">
        <Header :style="{padding: '0', position: 'fixed', width: `calc(100% - 200px)`, zIndex: 100}" :class="{'layout-header-bar': true, 'toggleWidth': this.widthFlag}">
          <Icon @click.native="collapsedSider" :class="rotateIcon" :style="{margin: '0 20px'}" type="md-menu" size="24"></Icon>
          <div class="user-info-box">
            <div class="icons-box">
              <Icon type="md-notifications-outline" size="24" />
            </div>
            <span>丨</span>
            <Dropdown>
              <a href="javascript:void(0)" :style="{color: '#666', marginRight: '10px'}">
                admin
                <Icon type="ios-arrow-down"></Icon>
              </a>
              <DropdownMenu slot="list">
                <DropdownItem>我的值班</DropdownItem>
                <DropdownItem>我的计划</DropdownItem>
                <DropdownItem>注销</DropdownItem>
              </DropdownMenu>
            </Dropdown>
            <Avatar icon="ios-person"/>
          </div>
        </Header>
        <Content :style="{margin: '88px 20px 20px 20px', background: '#fff', minHeight: '100vh', height: 'auto', marginBottom: '50px'}">
          <router-view></router-view>
        </Content>
      </Layout>
    </Layout>
  </div>
</template>
<script>
import AppMenu from '@/components/AppMenu'
export default {
  data () {
    return {
      isCollapsed: false,
      layoutStyle: {
        marginLeft: '200px'
      },
      styleObj: {
        padding: 0,
        position: 'fixed',
        zIndex: 100
      },
      widthFlag: false
    }
  },
  watch: {
    isCollapsed (val, oldVal) {
      console.log('val:' + val)
      if (val !== oldVal && val === true) {
        this.widthFlag = true
      } else {
        this.widthFlag = false
      }
    }
  },
  components: {
    AppMenu
  },
  computed: {
    rotateIcon () {
      return [
        'menu-icon',
        this.isCollapsed ? 'rotate-icon' : ''
      ]
    }
  },
  methods: {
    collapsedSider () {
      this.bus.$emit('triggerCollapse', this.$refs.side1.toggleCollapse)
    }
  },
  mounted () {
    let that = this
    this.bus.$on('triggerCollapse', function (func) {
      func()
      that.layoutStyle.marginLeft = that.isCollapsed ? '78px' : '200px'
    })
    this.layoutStyle.marginLeft = this.isCollapsed ? '78px' : '200px'
  }
}
</script>
<style scoped lang="stylus">
  @font-face {
    font-family: "custom-font";
    src: url('data:application/x-font-woff;charset=utf-8;base64,') format('woff');
  }
  .user-info-box
    display inline-block
    height 64px
    float right
    vertical-align middle
    margin 0 20px
    .icons-box
      display inline-block
      margin-right 20px
  .layout
    border: 1px solid #d7dde4
    background: #f5f7f9
    position: relative
    border-radius: 4px
    overflow: hidden
    .layout-header-bar
      background: #fff
      box-shadow: 0 1px 1px rgba(0,0,0,.1)
      width calc(100% - 200px)
      .rotate-icon
        transform: rotate(-90deg)
  .toggleWidth
    width calc(100% - 78px)!important
</style>



